import { Top, Bot } from 'api/team'
import { ShowToast } from 'common/js/loading-toast'
import { UserInfo } from 'common/js/common'

export default {
	data() {
		return {
			top: {},
			isBot: false,
			botArray: [],
			oIndex: -1,
			selected: "1",
			userInfo: {}
		}
	},
	methods: {
		select() {
			if(this.isBot) return;
			if(this.selected == '2') {
				Bot(this.userInfo.parentId).then(res => {
					console.log(res);
					if(res.data.code === 0) {
						this.isBot = true;
						for(let i = 0; i < res.data.data.length; i++) {
							this.botArray = res.data.data;
						}
					}
				}).catch(res => {})

			}
		},
		show(index) {
			this.oIndex = index;
		}
	},
	mounted() {
		this.userInfo = UserInfo();
		Top(this.userInfo.parentId).then(res => {
			console.log(res)
			if(res.data.code === 0) {
				this.top = res.data.data;
			}
		}).catch(res => {

		})
	}
}



